/*
  This boilerplate is based in ITCSS and SMACSS
*/
@import url("https://fonts.googleapis.com/css?family=Poppins");
@import url("https://fonts.googleapis.com/css?family=Open+Sans:700");
*,
*::before,
*:after {
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

body {
  margin: 0;
  line-height: 1.6;
  font-family: "Poppins", sans-serif;
  background-color: #FCFCFC;
  color: #53575A;
}

h1, h2, h3, h4, h5, h6 {
  font-family: "Open Sans", sans-serif;
  margin: 0;
}

img {
  max-width: 100%;
  width: 100%;
  height: 100%;
  display: block;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

.gallery {
  position: relative;
}

.gallery-figure {
  height: 100vh;
  margin: 0;
  display: none;
}

.gallery-figure.show {
  display: block;
  animation: opacity .5s linear;
}

@keyframes opacity {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.gallery-image {
  object-fit: cover;
}

.gallery-dot {
  width: 12px;
  height: 12px;
  background: white;
  border-radius: 50%;
  opacity: .8;
  border: 1px solid black;
}

@media screen and (min-width: 1024px) {
  .gallery-dot {
    width: 18px;
    height: 18px;
  }
}

.gallery-dot.selected {
  background: #1190C5;
  width: 20px;
  height: 20px;
  opacity: 1;
  animation: selected .5s linear;
}

@media screen and (min-width: 1024px) {
  .gallery-dot.selected {
    width: 25px;
    height: 25px;
  }
}

@keyframes selected {
  from {
    background: rgba(0, 0, 0, 0.4);
  }
  to {
    background: #1190C5;
  }
}

.gallery-container {
  width: 100%;
  overflow-y: hidden;
}

.gallery-container__images {
  overflow-y: hidden;
  height: 100vh;
  width: 100%;
}

.gallery-container__dots {
  position: absolute;
  z-index: 10;
  bottom: 1em;
  left: 50%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 70%;
  transform: translate(-50%);
}

@media screen and (min-width: 1024px) {
  .gallery-container__dots {
    width: 30%;
  }
}

.next {
  position: absolute;
  top: 45%;
  right: 1%;
  opacity: 1;
}

.next.disabled {
  opacity: .2;
}

.next::after {
  content: url("https://image.flaticon.com/icons/svg/180/180000.svg");
  position: absolute;
  top: 0;
  right: 0;
  width: 35px;
  height: 35px;
  z-index: 10;
  background: white;
  border-radius: 50%;
}

.prev {
  position: absolute;
  top: 45%;
  left: 1%;
}

.prev.disabled {
  opacity: .2;
}

.prev::after {
  content: url("https://image.flaticon.com/icons/svg/179/179983.svg");
  position: absolute;
  top: 0;
  left: 10%;
  width: 35px;
  height: 35px;
  z-index: 10;
  background: white;
  border-radius: 50%;
}

.main-header {
  background: #DD3F3B;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 .5em;
  margin-bottom: 2em;
}

.main-header__title {
  text-transform: uppercase;
}

.main-header__link {
  color: #FFF;
  margin-left: 1em;
}

.movies-container__list {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.movies-container__list .movies-item {
  width: 90%;
  margin-bottom: 2em;
  text-align: center;
  padding: 0.5em .3em;
  border: 0.1px solid #DFE0E0;
  box-shadow: 0 2px 2px #DFE0E0;
  position: relative;
  overflow: hidden;
}

.movies-container__list .movies-item__info {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  bottom: -100%;
  background: rgba(0, 0, 0, 0.9);
  transition: bottom .3s ease;
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.movies-container__list .movies-item:hover .movies-item__info {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  bottom: 0;
}

.movies-container__list .movies-item__title {
  color: #1190C5;
}

.movies-container__list .movies-item__image {
  margin: .5em auto;
  width: 130px;
  height: 160px;
}

.movies-container__list .movies-item__stars {
  font-size: 0.9em;
}

.main {
  display: flex;
  flex-wrap: wrap;
}

.main-title {
  text-align: center;
  width: 100%;
  color: #DD3F3B;
}

.main-link {
  display: inline-block;
  width: 100%;
  text-align: center;
  color: #282D31;
}
