*,
*::before,
*:after {
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

body {
  margin : 0;
  line-height: 1.6;
  font-family : $font-text;
  background-color: $body-bg;
  color: $font-color;
}

h1, h2, h3, h4, h5, h6 {
  font-family: $font-title;
  margin: 0;
}

img {
  max-width: 100%;
  width: 100%;
  height: 100%;
  display: block;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

